@charset "UTF-8";
.article-grid {
  grid-template-columns: 1fr 1fr 1fr;
}
.article-grid > *:first-child {
  grid-column: 1/3;
}
@media (max-width: 768px) {
  .article-grid {
    grid-template-columns: 1fr;
  }
}
.article-grid h1 {
  font-weight: 900;
  line-height: 44px;
}
@media (max-width: 768px) {
  .article-grid h1 {
    font-size: 2rem;
  }
}
@media (max-width: 550px) {
  .article-grid h1 {
    font-size: 1.5rem;
    line-height: 28px;
  }
}
.article-grid h2 {
  font-size: 1.5rem;
}
.article-grid h3 {
  font-size: 1.25rem;
}
.article-grid .article-sources a {
  color: var(--bs-gray);
  font-size: 0.875rem;
}
.article-grid .article-conclusion {
  color: var(--bs-gray) !important;
  font-size: 1rem;
}
.article-grid__item p {
  font-size: 1.125rem;
  color: var(--bs-blue);
  line-height: 1.5;
  margin: 1rem 0;
}
@media (max-width: 768px) {
  .article-grid__item p {
    font-size: 1rem;
  }
}
.article-grid__item p a {
  text-decoration: underline;
  color: var(--orange-color);
}
.article-grid__item:first-child ul,
.article-grid__item ol {
  font-size: 1.125rem;
}
@media (max-width: 768px) {
  .article-grid__item:first-child ul,
  .article-grid__item ol {
    font-size: 1rem;
  }
}
.article-grid__item h2,
.article-grid__item h3,
.article-grid__item h4,
.article-grid__item h5,
.article-grid__item h6 {
  font-weight: 700;
}
.article-grid__item img {
  width: 100%;
}

.article-list li {
  position: relative;
  font-size: 1.125rem;
  color: var(--bs-blue);
  line-height: 1.5;
  padding-left: 1rem;
}
@media (max-width: 768px) {
  .article-list li {
    font-size: 1rem;
  }
}
.article-list li::before {
  content: "";
  position: absolute;
  left: 0;
  /* Позиционируем точку слева от текста */
  top: 9px;
  width: 4px;
  height: 4px;
  background-color: var(--yellow-color);
  /* Цвет точки совпадает с цветом текста */
  border-radius: 50%;
  /* Делаем элемент круглым */
}

.article-anchors {
  background: var(--light-background);
  height: fit-content;
  position: sticky;
  top: 1rem;
}
@media (max-width: 992px) {
  .article-anchors {
    top: 9rem;
  }
}
.article-anchors a {
  color: var(--bs-gray);
  position: relative;
  padding-left: 0.875rem;
  transition: all 0.3s ease;
}
.article-anchors a.active {
  color: var(--bs-blue);
}
.article-anchors a:hover {
  color: var(--orange-color);
}
.article-anchors a::before {
  content: "";
  position: absolute;
  left: 0;
  /* Позиционируем точку слева от текста */
  top: 50%;
  transform: translateY(-50%);
  width: 4px;
  height: 4px;
  background-color: currentColor;
  /* Цвет точки совпадает с цветом текста */
  border-radius: 50%;
  /* Делаем элемент круглым */
}

.info-article__text {
  color: var(--bs-gray) !important;
  font-size: 1rem !important;
}
.info-article__name {
  font-size: 1rem !important;
}

.cards-article__item {
  border: 1px solid var(--border-light);
  transition: all 0.3s ease;
}
@media (max-width: 550px) {
  .cards-article__item img {
    max-height: 61px;
  }
}
.cards-article__item p {
  color: var(--bs-gray);
  line-height: 1.5;
  font-size: 1rem;
  transition: all 0.3s ease;
}
.cards-article__item:hover {
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.1);
}
.cards-article__item:hover p {
  color: var(--orange-color);
}

@media (max-width: 768px) {
  .article-socials {
    width: 100%;
  }
}

.socials-article__block {
  background: var(--bs-white);
  border: 1px solid var(--border-light);
}
@media (max-width: 768px) {
  .socials-article__block:first-child {
    border: none;
  }
}
.socials-article button {
  background: var(--extra-light-background);
  border-radius: 0.5rem;
  border: none;
  color: inherit;
}
.socials-article__icon {
  height: fit-content;
  background: var(--extra-light-background);
  border-radius: 0.5rem;
}
@media (max-width: 768px) {
  .socials-article__icon {
    width: 100%;
  }
}
.socials-article__title {
  font-size: 1rem !important;
}

.article-video {
  min-height: 670px;
}

.icon_telegram {
  width: 20px;
  height: 17px;
  color: var(--bs-blue);
}

.icon_whatsapp {
  width: 20px;
  height: 20px;
  color: var(--bs-blue);
}

.icon_vk {
  width: 18px;
  height: 18px;
  color: var(--bs-blue);
}

.like {
  width: 18px;
  height: 19px;
}

.dislike {
  transform: scaleY(-1);
  width: 18px;
  height: 19px;
}

.pencil {
  width: 18px;
  height: 18px;
}

.clock {
  width: 20px;
  height: 20px;
}